@import '../../css/helpers/variables.css';

/* css header  */
.header-vieme {
    background-color: #4169E1;
    width: 100%;
    height: var(--heightHeader);
}

/*  css container header */

.container-Header {
    margin-right: var(--marginHeader);
    margin-left: var(--marginHeader);
    display: flex;
    justify-content: space-between;
}

/* css header left */

.header-vieme .header-left {
    display: flex;
    line-height: var(--heightHeader);
    justify-content: start;
}

.header-vieme .logo-circle {
    width: var(--heightHeader);
    height: var(--heightHeader);
    vertical-align: middle;
    border-radius: 50%;
}

.header-vieme .logo-header {
    padding: 8px;
    border-radius: 50px;
}

.header-vieme .logo-header {
    padding: 8px;
    width: 100%;
    height: 100%;
}

.header-vieme .header-left .Name-Company {
    margin-right: 8px;
}

.header-vieme .header-left .Name-Company h1{
    color: #ffffff;
    font-size: 26px;
}

/* css search form - center header */

.header-vieme .header-center {
    width: 500px;
}

.header-vieme .SearchForm {
    margin-top: calc((var(--heightHeader) - var(--heightSearchFormHeader)) / 2);
    margin-bottom: calc((var(--heightHeader) - var(--heightSearchFormHeader)) / 2);
    width: 100%;
}

.header-center .item-centerHeader {
    display: none;
}

/* css header right */

.header-vieme .header-right {
    display: flex;
    line-height: var(--heightHeader);
    justify-content: end;
}

.header-vieme .header-right .svg-inline--fa {
    color: #fff;
    font-size: 20px;
    padding-top: calc((var(--heightHeader) - var(--font-SizeIconHeader)) / 2);
    padding-bottom: calc((var(--heightHeader) - var(--font-SizeIconHeader)) / 2);
    margin: 0 var(--margin-IconHeader) 0 var(--margin-IconHeader);
    color: var(--coloriconHeader);
}

.header-right .avatar-circle {
    width: var(--heightHeader);
    height: var(--heightHeader);
    vertical-align: middle;
    border-radius: 50%;
}

.header-right .avatar-header {
    width: 100%;
    height: 100%;
    padding: 12px;
    border-radius: 50%;                                                                                  
}
