:root {
    --heightHeader: 56px;
    --heightSearchFormHeader: 36px;
    --font-SizeIconHeader: 20px;
    --coloriconHeader: #dcedf8;
    --marginHeader: 8px;
    --margin-IconHeader: 16px;
}

/* Responsive tablet */
@media (max-width: 1024px)  {
    :root {
        --marginHeader: 16px;
    }
}

/* Responsive mobile */
@media (max-width: 767px)  {
    :root {
        --marginHeader: 8px;
        --margin-IconHeader: 8px;
    }

    .header-vieme .header-left .Name-Company h1{
        color: #ffffff;
        font-size: 20px !important;
        margin-right: 8px;
    }

    .header-vieme .header-center .svg-inline--fa {
        color: #fff;
        font-size: 20px;
        padding-top: calc((var(--heightHeader) - var(--font-SizeIconHeader)) / 2);
        padding-bottom: calc((var(--heightHeader) - var(--font-SizeIconHeader)) / 2);
        margin: 0 var(--margin-IconHeader) 0 var(--margin-IconHeader);
        color: var(--coloriconHeader);
    }

    .header-vieme .header-center .item-centerHeader {
        display: block !important;
    }

    .header-vieme .header-center {
        display: flex;
        justify-content: end;
    }
}



